/* ==========================================================================
   Progress Bars
   ========================================================================== */
/**
 * Table of Contents:
 *
 *  1. Declarations
 *  2. Loader
 */
/*********************
  1. Declarations
**********************/
/*********************
  2. Loader
**********************/
.loader {
  border: 5px solid #d8d8d8;
  border-top: 5px solid #b2120f;
  border-radius: 50%;
  width: 25px;
  height: 25px;
  animation: spin 2s linear infinite;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

/* ==========================================================================
   Inputs
   ========================================================================== */
/**
 * Table of Contents:
 *
 *  1. Declarations
 *  2. Codearea
 */
/*********************
  1. Declarations
**********************/
/*********************
  2. Codearea
**********************/
.ce-element--type-image:not(.img-center), .ce-element--type-video:not(.img-center) {
  display: block;
  margin-left: 0 !important;
  margin-right: 0 !important;
}

/* ==========================================================================
   Media
   ========================================================================== */
/**
 * Table of Contents:
 *
 *  1. Declarations
 *  2. Images
 *  3. Videos
 */
/*********************
  1. Declarations
**********************/
/*********************
  2. Images
**********************/
.avatar {
  width: 120px;
  height: 120px;
  background-size: cover;
  background-repeat: no-repeat;
  border-radius: 50%;
  position: relative;
}

.avatar.large {
  width: 200px;
  height: 200px;
}

.avatar.small {
  width: 50px;
  height: 50px;
}

.avatar.x-small {
  width: 35px;
  height: 35px;
}

.avatar.center {
  margin: 0 auto;
}

.thumbnail {
  width: 100%;
  border: 2px solid #fff;
}

.img-responsive {
  width: 100% !important;
}

.img-center {
  margin-left: auto !important;
  margin-right: auto !important;
  display: block !important;
}

/*********************
  3. Videos
**********************/
video.fullscreen {
  position: fixed;
  top: 50%;
  left: 50%;
  min-width: 100%;
  min-height: 100%;
  width: auto;
  height: auto;
  z-index: -100;
  -ms-transform: translateX(-50%) translateY(-50%);
  -moz-transform: translateX(-50%) translateY(-50%);
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

