.btn {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 14px;
  color: #fff;
  font-weight: 600;
  white-space: nowrap;
  border: 2px solid #b2120f;
  border-radius: 5px;
  background-color: #b2120f;
  cursor: pointer;
  -webkit-transition: all 0.3s;
  -o-transition: all 0.3s;
  -ms-transition: all 0.3s;
  transition: all 0.3s;
}

.btn.active, .btn:active {
  -webkit-box-shadow: none;
  box-shadow: none;
}

.btn:focus, .btn:active:focus, .btn.active:focus {
  outline: none;
}

.btn:hover, .btn:active, .btn.hover, .btn.active {
  color: #b2120f;
  background-color: transparent;
}

.btn i:first-child {
  margin-right: 5px;
}

.btn i:last-child {
  margin-left: 5px;
}

.btn.btn-transparent {
  color: #b2120f;
  background-color: transparent;
}

.btn.btn-transparent:hover, .btn.btn-transparent:active, .btn.btn-transparent.hover, .btn.btn-transparent.active {
  background-color: #b2120f;
  color: #fff;
}

