/* ==========================================================================
   Inputs
   ========================================================================== */
/**
 * Table of Contents:
 *
 *  1. Declarations
 *  2. General
 *  3. Placeholder
 *  4. Form Group
 *  5. Form Group Addon
 */
/*********************
  1. Declarations
**********************/
/*********************
  2. General
**********************/
input,
textarea,
input[type="submit"]:focus,
button:focus {
  outline: none;
}

label,
input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  vertical-align: top;
  margin: 0;
  border-radius: 0;
  font-size: inherit;
  line-height: normal;
}

input[type="submit"],
input[type="reset"],
input[type="button"] {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

input[type="checkbox"] {
  -webkit-appearance: checkbox;
  -moz-appearance: checkbox;
  appearance: checkbox;
  margin: 0;
}

input[type="radio"] {
  -webkit-appearance: radio;
  -moz-appearance: radio;
  appearance: radio;
  margin: 0;
}

.form-control {
  background-color: #fff;
  padding: 0 16px;
  font-size: 1rem;
  font-weight: 500;
  color: #1D2127;
  width: 100%;
  height: 35px;
  margin: 0;
  flex: 1;
  text-overflow: ellipsis;
  border: 1px solid #555;
  box-shadow: none !important;
  border-radius: 4px;
}

.form-control.has-addon {
  border-right: none;
}

textarea.form-control {
  padding: 14px 16px;
  min-height: 140px;
  resize: vertical;
  overflow: auto;
  text-transform: none;
}

.ie8 .form-control {
  line-height: 35px;
}

.form-control:focus {
  outline: none;
}

/*********************
  3. Placeholder
**********************/
:-moz-placeholder {
  font-size: 1rem;
  font-weight: 500;
  color: #d8d8d8;
}

::-moz-placeholder {
  font-size: 1rem;
  font-weight: 500;
  color: #d8d8d8;
}

:-ms-input-placeholder {
  font-size: 1rem;
  font-weight: 500;
  color: #d8d8d8;
}

::-webkit-input-placeholder {
  font-size: 1rem;
  font-weight: 500;
  color: #d8d8d8;
}

input:focus:-moz-placeholder {
  color: transparent !important;
}

input:focus::-moz-placeholder {
  color: transparent !important;
}

/*********************
  4. Form Group
**********************/
.form-group {
  position: relative;
  display: flex;
  margin: 25px 0 15px 0;
}

.form-group * {
  -webkit-transition: border-color ease-in-out 0.15s;
  -o-transition: border-color ease-in-out 0.15s;
  -ms-transition: border-color ease-in-out 0.15s;
  transition: border-color ease-in-out 0.15s;
  -webkit-transition: box-shadow ease-in-out 0.15s;
  -o-transition: box-shadow ease-in-out 0.15s;
  -ms-transition: box-shadow ease-in-out 0.15s;
  transition: box-shadow ease-in-out 0.15s;
}

.form-group label {
  font-size: 0.9rem;
  color: #f2f2f2;
  position: absolute;
  top: 0;
  left: 0;
  margin-top: -19.4444444444px;
}

.form-group label:after {
  display: block;
  content: "";
  position: absolute;
  top: 56px;
  opacity: 0;
  white-space: nowrap;
  transition: .2s opacity ease-out, .2s color ease-out;
}

.form-group.active label {
  color: #b2120f;
}

.form-group.active .form-control, .form-group.active .form-group-addon {
  border-color: #b2120f;
  box-shadow: none;
}

.form-group.has-success label:after, .form-group.has-error label:after {
  content: attr(data-message);
  opacity: 1;
  font-size: 0.8rem;
}

.form-group.has-error label {
  color: #d2322d;
}

.form-group.has-error label:after {
  color: #d2322d;
}

.form-group.has-error .form-control, .form-group.has-error .form-group-addon {
  border-color: #d2322d;
  box-shadow: none;
}

.form-group.has-success label {
  color: #47a447;
}

.form-group.has-success label:after {
  color: #47a447;
}

.form-group.has-success .form-control, .form-group.has-success .form-group-addon {
  border-color: #47a447;
  box-shadow: none;
}

